<template>
  <div id="user">
    <div class="breadcrumb-box">
      <Breadcrumb>
        <Breadcrumb-item href="/home"> 首页 </Breadcrumb-item>
        <Breadcrumb-item> 阅读管理 </Breadcrumb-item>
      </Breadcrumb>
    </div>
    <Row>
    </Row>

    <Modal
      title="用户编辑"
      v-model="userPanel"
      :mask-closable="false"
      :loading="true"
      @on-ok="setUser"
      width="720">
      <Row>
        <i-col span="20" offset="2">
          <i-form v-model="handleUser" :label-width="100">
            <Form-item label="用户编号">
              <i-input v-model="handleUser.id" disabled></i-input>
            </Form-item>
            <Form-item label="登录名">
              <i-input v-model="handleUser.username" disabled></i-input>
            </Form-item>
            <Form-item label="别称">
              <i-input v-model="handleUser.alias" placeholder="不填写则不修改"></i-input>
            </Form-item>
            <Form-item label="重置密码">
              <i-input v-model="handleUser.password" placeholder="不填写则不修改"></i-input>
            </Form-item>
            <Form-item label="用户组变更">
              <i-select v-model="handleUser.group_id">
                <i-option v-for="item in groupList" :value="item.gid">{{ item.name }}</i-option>
              </i-select>
            </Form-item>
            <Form-item label="用户状态">
              <Radio-group v-model="handleUser.status" type="button" size="small" style="float: left;">
                <Radio label="1">正常</Radio>
                <Radio label="0">锁定</Radio>
              </Radio-group>
            </Form-item>
          </i-form>
        </i-col>
      </Row>
    </Modal>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        self: this,
        userPanel: false,
        formInline: {
          field: 'id',
          value: '',
          thisGid: 0,
          thisStatus: 1
        },
        handleUser: {},
        groupList: [
          { gid: 1, name: '超级管理员' },
          { gid: 2, name: '编辑' },
          { gid: 3, name: '会员' }
        ],
        statusList: [
          { status: -1, name: '所有状态' },
          { status: 0, name: '锁定' },
          { status: 1, name: '正常' },
          { status: 2, name: '删除' }
        ],
        list: {
          field: [
            {
              title: '编号',
              key: 'id',
              align: 'center',
              width: '80px'
            },
            { title: '用户名', key: 'username' },
            { title: '别称', key: 'alias' },
            { title: '注册时间', key: 'create_at' },
            { title: '用户组', key: 'group_name' },
            { title: '状态', key: 'status_name' },
            {
              title: '操作',
              key: 'action',
              align: 'center',
              render (row, columen, index) {
                return `<i-button type="info" size="small" @click.native="showEdit(${index})">操作</i-button> <i-button type="error" size="small" @click.native="showDelete(${index})">删除</i-button>`
              }
            }
          ],
          data: [
            {
              id: 1,
              username: 'zhangsan',
              alias: '张三',
              create_at: '2017-03-01 12:12',
              group_id: 3,
              group_name: '会员',
              status: 1
            },
            {
              id: 2,
              username: 'zhangsan',
              alias: '张三',
              create_at: '2017-03-01 12:12',
              group_id: 2,
              group_name: '会员',
              status: 1
            },
            {
              id: 3,
              username: 'zhangsan',
              alias: '张三',
              create_at: '2017-03-01 12:12',
              group_id: 3,
              group_name: '会员',
              status: 1
            },
            {
              id: 4,
              username: 'zhangsan',
              alias: '张三',
              create_at: '2017-03-01 12:12',
              group_id: 3,
              group_name: '会员',
              status: 1
            },
            {
              id: 5,
              username: 'zhangsan',
              alias: '张三',
              create_at: '2017-03-01 12:12',
              group_id: 2,
              group_name: '会员',
              status: 1
            },
            {
              id: 6,
              username: 'zhangsan',
              alias: '张三',
              create_at: '2017-03-01 12:12',
              group_id: 1,
              group_name: '会员',
              status: 1
            }
          ]
        }
      }
    },
    created: function () {
      for (var item in this.list.data) {
        this.list.data[item].group_name = this.getGroupName(this.list.data[item].group_id)
        this.list.data[item].status_name = this.list.data[item].status === 1 ? '正常' : '不可用'
      }
    },
    methods: {
      handleSubmit () {
        console.log(this.formInline)
      },
      showEdit (index) {
        this.handleUser = this.list.data[index]
        this.userPanel = true
      },
      showDelete (index) {
        this.$Modal.confirm({
          title: '确定删除?',
          content: '那就删除吧~',
          loading: true,
          onOk: () => {
            setTimeout(() => {
              this.$Modal.remove()
              this.$Message.success('用户已删除')
            }, 2000)
          },
          cancelText: '取消'
        })
      },
      getList (page) {
        console.log(page)
      },
      setUser () {
        // 异步修改用户信息
        console.log(this.handleUser)
        this.$Modal.remove()
        this.$Message.success('操作成功')
      },
      getGroupName (gid) {
        for (let index = 0; index < this.groupList.length; index++) {
          if (gid === this.groupList[index].gid) {
            return this.groupList[index].name
          }
        }
        return '无分组'
      }
    }
  }
</script>

<style>
  .table-box {
    width: 98%;
    margin: 10px 1%;
    position: relative;
    top: 0;
    bottom: 0;
    overflow: hidden;
  }
  .table-page {
    display: block;
    float: right;
    margin: 15px 10px;
  }
</style>



